.MovieContainer {
  color: #fff;
  background: #0f2027; /* fallback for old browsers */
  background: webkit-linear-gradient(
    to right,
    #2c5364,
    #203a43,
    #0f2027
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #2c5364,
    #203a43,
    #0f2027
  );
  padding-top: 50px;
  background: #101522;/* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.MovieWrapper {
  display: grid;
  z-index: 1;
  /* height: calc(500px + 5vw); */
  width: 100%;
  max-width: 1100px;
  margin-top: 5vw;
  margin-right: auto;
  margin-left: auto;
  padding: 0 24px;
  grid-gap: 16px;
}

.MovieRow {
  /* align-items: center; */

  display: flex;
  flex-direction: row;
  flex-flow: row wrap;
  justify-content: space-between;
  flex-wrap: wrap;
  margin-bottom: 20px;
}

.Column1 {
  /* margin-top: calc(-50px + 1vw); */
  /* margin-left: 4vw; */
  /* margin-bottom: 15px; */
  /* padding: 0 15px; */
  width: 300px;
  grid-area: col1;
}

.Column2 {
  /* margin-top: 100px; */
  /* margin-left: 4vw; */
  /* margin-bottom: 0px; */
  /* padding: 0 15px; */
  width: 300px;
  grid-area: col2;
}

.Column3 {
  /* margin-bottom: 15px; */
  /* padding: 0 15px; */

  grid-area: col3;
  /* margin-top: -50px; */
  /* align-items: center; */
}
.Img {
  /* margin: 150px 0 10px 0; */
  padding-right: 0;
  border-radius: 10px;
  border: 2px solid white;
}

.ImgWrap {
  max-width: 555px;
  height: 50%;
}

.Heading {
  /* margin-bottom: 16px; */
  font-size: calc(24px + 1vw);
  line-height: 1.1;
  font-weight: 600;
}

.Year {
  color: white;
  font-size: calc(14px + 0.5vw);
  line-height: 16px;
  font-weight: 700;
  letter-spacing: 1.4px;
  text-transform: uppercase;
  margin-bottom: 16px;
}

.Genre {
  color: #01bf71;
  font-size: calc(14px + 0.5vw);
  line-height: 16px;
  font-weight: 700;
  letter-spacing: 1.4px;
  text-transform: uppercase;
  margin-bottom: 0px;
}

.Syno {
  /* margin-left: 4vw;
  margin-top: 50px; */
  font-size: 16px;
  line-height: 16px;
  font-weight: 700;
  letter-spacing: 1.4px;
  text-transform: uppercase;
  margin-bottom: 16px;
}

.Description {
  /* margin-left: 4vw; */
  max-width: 440px;
  margin-bottom: 35px;
  font-size: calc(12px + 0.2vw);
  line-height: 24px;
}

.icons {
  margin-top: 2vw;
  cursor: pointer;
}

.likesCount {
  margin-top: -25px;
  margin-left: 30px;
}

.fi {
  margin-right: 20px;
}

.profileuserimg {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  object-fit: cover;
  position: absolute;
  margin-top: 20px;

  border: 3px solid white;
}

.commenttrailerWrapper {
  display: grid;
  z-index: 1;
  height: auto;
  width: 100%;

  margin-top: calc(50px + 3vw);
  margin-right: auto;
  margin-left: auto;
  padding: 0 24px;
  justify-content: center;
}

.commentRow {
  display: flex;
  flex-direction: row;
  flex-flow: row wrap;
  justify-content: center;
  flex-wrap: wrap;
  margin-bottom: 20px;
}

.commentSection {
  /* margin-top: calc(-50px + 1vw); */
  /* margin-left: 3vw; */
  margin-top: 20px;
  margin-bottom: 15px;
  grid-area: "cl2";
  justify-content: center;
  
}

.trailer {
  /* margin-left: calc(1px + 2.5vw); */
  padding: 0 40px;
  align-items: center;
  grid-area: "cl1";
  margin-top: calc(1vw);
}
    .commentSection {
      width: auto;
      
    }
.comments {
  width: calc(280px + 3vw);
  border-radius: 10px;
  -webkit-box-shadow: 0px 1px 7px 0px rgba(0, 0, 0, 0.37);
  box-shadow: 0px 1px 7px 0px rgba(0, 0, 0, 0.37);
  margin: 20px 20px 20px 20px;
  background-color: #50505B;
}

.commentwrapper {
  padding: 10px;
  display: flex;
  flex: 0.7;
}

.commenting {
  border: none;
  width: 100%;
  color: #fff;
}

.commentinput {
  border: none;
  width: 60%;
  color: #fff;
  background-color: transparent;
}

.commentinput:focus {
  outline: none;
}
.commenttitle {
  font-size: calc(16px + 0.2vw);
  font-style: bold;
  margin-left: 5px;
  color: #fff;
}
.commentbutton {
  border: none;
  width: 70px;
  height: 40px;
  padding: 7px;
  border-radius: 5px;
  background-color: #01bf71;
  font-size: 16px;
  font-weight: 500;
  margin-left: 10px;
  cursor: pointer;
  color: white;
  float: right;
}

.tbutton {
  margin-top: 20px;
  width: 150px;
  height: 50px;
  color: white;
  background-color: #01bf71;
  border-radius: 25px;
  border: none;
  cursor: pointer;
  padding: 2px;
  font-size: 15px;
}

.playercontainer {
  width: 100%;
  height: 1000px;
  justify-content: center;

  background: #0f2027; /* fallback for old browsers */
  background: webkit-linear-gradient(
    to right,
    #2c5364,
    #203a43,
    #0f2027
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #2c5364,
    #203a43,
    #0f2027
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  align-items: center;
  padding: 50px;
}

.watchplayer {
  width: 95%;
  margin-top: 100px;
  margin-left: calc(1.2vw + 20px);
  border-radius: 50px;
  justify-self: center;
  align-items: center;
}

@media screen and (max-width: 768px) {
  .MovieWrapper {
    margin-top: 20vw;
  }
  .Img {
    /* margin: 150px 0 10px 0; */
    padding-right: 0;
    border-radius: 10px;
    max-width: 145px;
  }

  .ImgWrap {
    max-width: 145px;
    height: 50%;
    align-self: center;
  }

  .Column3 {
    margin-top: 20px;
  }

  .Column1 {
    width: 150px;
  }
  .Column2 {
    width: 150px;
  }

  .likesCount {
    font-size: 15px;
  }

  .commentRow {
    width: 400px;
  }

  .trailer {
    width: auto;
    justify-content: center;
    align-items: center;
  }


}
