.MenuContainer{      
    justify-content: center;
    padding: 0 30px;
    height: 550px;
    width: 100%;
    align-items: center;
    background-color: transparent;
      
}

.searchbar{ 
    margin:68px;
    width: calc(45vw + 70px);
    font-size: 12px;
};


.headingWrapper{
    display: flex;
    flex-direction: column;
}




.mHeading{
   
   font-size: 25px;
   color: white;
}



.btn-modal {
    padding: 10px 20px;
    display: block;
    margin: 100px auto 0;
    font-size: 18px;
}

.modal, .overlay {
    width: 100vw;
    height: 100vh;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    position: fixed;
}

.overlay {
    background: rgba(49,49,49,0.8);
}
.modal-content {
    position: absolute;
    top: 40%;
    left: 50%;
    transform: translate(-50%, -50%);
    line-height: 1.4;
    background: #f1f1f1;
    padding: 14px 28px;
    border-radius: 3px;
    max-width: 600px;
    min-width: 300px;
}

.close-modal {
    position: absolute;
    top: 10px;
    right: 10px;
    padding: 5px 7px;
}



.card{
    background: #fff;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    height: 360px;
    max-height: 340px;
    box-shadow: 65px;
    transition: all 0.2s ease-in-out;
    float: left;
    margin-left: 3.3rem;
    margin-top: 1rem;
    margin-bottom: 30px;
    border-radius: 10px;
    width: 240px;

}

.mimg{

    height: 300px;
    transition: all 0.2s ease-in-out;
    border: 1px solid white;
  
}


 
.mimg:hover{
        transform: scale(1.02);
        transition: all 0.4s ease-in-out;
        cursor: pointer;
 };

 @media screen and (max-width: 768px){
    .card {
        margin-top: 0rem;
    }
};

@media screen and (max-width: 480px){
    .card {
        margin-top: 0rem;
    }
};

.carousel{
   
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh;
    float: left;
}

body.active-modal {
    overflow-y: hidden;
}

